"use client";

import { useState, useEffect } from "react";
import { useRouter } from "next/navigation";
import { Button } from "@/app/components/ui/button";
import { Input } from "@/app/components/ui/input";
import { Label } from "@/app/components/ui/label";
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/app/components/ui/card";
import { toast, Toaster } from "sonner";
import { ShieldCheck } from "lucide-react";

// ‚úÖ Dummy users with roles
const users = [
  { email: "superadmin@example.com", password: "super123", role: "superadmin" },
  { email: "admin1@example.com", password: "admin123", role: "admin" },
  { email: "admin2@example.com", password: "admin123", role: "admin" },
];

const Login = () => {
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [isLoading, setIsLoading] = useState(false);
  const router = useRouter();

  // ‚úÖ Redirect if already logged in
  useEffect(() => {
    if (typeof window !== "undefined") {
      const isAuthenticated = localStorage.getItem("isAuthenticated");
      if (isAuthenticated) {
        router.push("/dashboard");
      }
    }
  }, [router]);

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setIsLoading(true);

    try {
      // ‚úÖ Simulate API/DB login check
      await new Promise((resolve) => setTimeout(resolve, 1000));

      const user = users.find((u) => u.email === email && u.password === password);

      if (!user) {
        toast.error("‚ùå Invalid credentials. Use one of the demo accounts.", {
          duration: 4000, // toast 4 seconds show hoga
          theme: "shade", // Shade/CN theme
        });
        setIsLoading(false);
        return;
      }

      if (typeof window !== "undefined") {
        localStorage.setItem("isAuthenticated", "true");
        localStorage.setItem("userRole", user.role);
        localStorage.setItem("userEmail", user.email);
      }

      // ‚úÖ Success toast with Shade theme
      toast.success(`üéâ Login successful! Logged in as ${user.role}`, {
        duration: 4000, // 4 seconds
        theme: "shade",
      });

      // ‚úÖ Redirect after short delay
      setTimeout(() => {
        router.push("/dashboard");
      }, 500);
    } catch (error) {
      console.error("Login error:", error);
      toast.error("‚ùå Something went wrong. Please try again.", {
        duration: 4000,
        theme: "shade",
      });
    } finally {
      setIsLoading(false);
    }
  };

  return (
    <div className="min-h-screen flex items-center justify-center bg-background p-4">
      {/* ‚úÖ Toaster for notifications */}
      <Toaster position="top-right" richColors />

      <Card className="w-full max-w-md shadow-lg">
        <CardHeader className="space-y-1 text-center">
          <div className="flex justify-center mb-4">
            <div className="bg-primary/10 p-3 rounded-full">
              <ShieldCheck className="h-8 w-8 text-primary" />
            </div>
          </div>
          <CardTitle className="text-2xl font-bold">Admin Login</CardTitle>
          <CardDescription>
            Enter your credentials to access the admin panel
          </CardDescription>
        </CardHeader>
        <CardContent>
          <form onSubmit={handleSubmit} className="space-y-4">
            <div className="space-y-2">
              <Label htmlFor="email">Email</Label>
              <Input
                id="email"
                type="email"
                placeholder="admin@example.com"
                value={email}
                onChange={(e) => setEmail(e.target.value)}
                required
              />
            </div>
            <div className="space-y-2">
              <Label htmlFor="password">Password</Label>
              <Input
                id="password"
                type="password"
                placeholder="Enter password"
                value={password}
                onChange={(e) => setPassword(e.target.value)}
                required
              />
            </div>
            <Button type="submit" className="w-full" disabled={isLoading}>
              {isLoading ? "Signing in..." : "Sign In"}
            </Button>

            {/* ‚úÖ Demo accounts info */}
            <div className="text-xs text-muted-foreground text-center mt-4 space-y-1">
              <p>Superadmin: superadmin@example.com / super123</p>
              <p>Admin1: admin1@example.com / admin123</p>
              <p>Admin2: admin2@example.com / admin123</p>
            </div>
          </form>
        </CardContent>
      </Card>
    </div>
  );
};

export default Login;
